.App {
  height: 100vh;
  display: grid;
  grid-template-columns: [start] 1fr [contentstart] 3fr [contentend] 1fr [end];
  align-content: center;
}

.wrapper {
  grid-column: contentstart / contentend;
  height: 60vh;
  padding: 20px;
  border-radius: 15px;
  background-image: url(./fatCat.jpg);
  background-size: cover;
  background-position: center;
  margin: 20px;
}

.title {
  grid-column: contentstart / contentend;
  font-size: 24px;
  justify-self: center;
  color: #bd155b;
}

.inputs{
  padding: 10px;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap:10px;
  grid-column: contentstart / contentend;
  margin-bottom: 20px;
}

label {
  display: grid;
  grid-template-columns: 20px 1fr;
}

input {
  padding: 10px;
  border:none;
  min-width: 60px;
  
  border-radius: 3px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);
  
}

input:focus-visible{
  outline: none;
  box-shadow: 0 0 0 3px #bd155b;
}

.btn {
  grid-column: contentstart / contentend;
	text-decoration: none;
	color: #bd155b;
  margin: 0 150px;
	background-image: linear-gradient(90deg, #fd7f34, #bd155b);
	display: inline-block;
	padding:10px 10px;
	border: 1px solid;
	position: relative;
	z-index: 0;
	border-radius: 5px;
	box-sizing: border-box;
	outline: none;
	cursor: pointer;
	user-select: none;
	appearance: none;
	touch-action: manipulation;  
  background: transparent;
  font-size: 20px;
}
.btn:before {
	content: '';
	position: absolute;
	left: -2px;
	top: -2px;
	width: calc(100% + 4px);
	height: calc(100% + 4px);
	background: linear-gradient(90deg, #fd7f34, #bd155b);
	z-index: -2;
	transition: .4s;
	border-radius: 5px;
}
.btn:after {
	content: '';
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: linear-gradient(90deg, #fff, #fff);
	z-index: -1;
	transition: .4s;
	border-radius: 4px;
}
.btn:hover {
	color: #fff;
	transition: .3s;
}
.btn:hover:after {
	background: linear-gradient(90deg, #fd7f34, #bd155b);
}
.btn:active:after {
	background: linear-gradient(90deg, #d96d2d, #760f3a);
}
.btn:focus-visible {
	box-shadow: 0 0 0 3px #bd155b;
}

.words {
  padding: 10px;
  width: 240px;
  background-color: #edecee6b;
  border-radius: 5px;
}

.word {
  display: flex;
  justify-content: space-between;
  font-size: 30px;
  margin-bottom: 20px;
  color:#bd155b;
}
.last{
  margin: 0;
}
@media screen and (max-width:760px) {

  .App {
    grid-template-columns: [contentstart] 1fr [contentend];
    align-content: start;
    margin-top: 20px;
  }
  
  .wrapper {
    height: 30vh;
  }
  .inputs {
    grid-template-columns: repeat(2, 1fr);
  }

  .words {
    padding: 5px;
    width: 200px;
  }
  
  .word {
    display: flex;
    justify-content: space-between;
    font-size: 24px;
    margin-bottom: 15px;
  }

  .btn {
    margin: 0 20px;
  }
}