body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(105, 220, 255, 0.541);
   background-image: url(./cats/cat-1.png);
   background-size: 30%;
   animation: rotate 30s linear infinite;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@keyframes rotate{
 0% {
  background-position: -20% 20%;
 }
 10% {
  background-position: -17% 19%;
 }
 20% {
  background-position: -15% 16.5%;
 }
 30% {
  background-position: -15% 13.5%;
 }
 40% {
  background-position: -17% 11%;
 }
 50% {
  background-position: -20% 10%;
 }
 60% {
  background-position: -23% 11%;
 }
 70% {
  background-position: -25% 13.5%;
 }
 80% {
  background-position: -25% 16.5%;
 }
 90% {
  background-position: -23% 19%;
 }
 100% {
  background-position: -20% 20%;
 }
}
